import logo from './logo.svg';
import react, { useEffect } from "react"
import './App.css';
import Issues from "./Issues.js"
import Pagination from "./Pagination.js"

function App() {
  const [userName,setUserName]=react.useState("")
  const [repoName,setRepoName]=react.useState("")
  const [issuesArray,setIssuesArray]=react.useState([])
  const [isFetchDataSuccess,setIsFetchDataSuccess]=react.useState(true)
  const [currentPage,setCurrentPage]=react.useState(1)
  const [dataPerPage,setDataPerPage]=react.useState(4)
  function changeUserName(e){
    setUserName(e.target.value)
    setIssuesArray([])
  }
  function changeRepoName(e){
    setRepoName(e.target.value)
    setIssuesArray([])
  }
  function search(){
    fetch(`https://api.github.com/repos/${userName}/${repoName}/issues`).then(result=>{
      console.log(result)
      if(!result.ok)
      setIsFetchDataSuccess(false)
      else
      setIsFetchDataSuccess(true)
      return result.json()
      
    }).
    then(finalObject=>{
      console.log(finalObject)
      setIssuesArray(finalObject)})
  }
  function changeCurrentPage(pageNumber){
    console.log("KUI")
    setCurrentPage(pageNumber)
  }
  console.log("issues",issuesArray)
  //console.log(`username ${userName}`)
  //console.log(`reponame ${repoName}`)
  return (<div>
     <div id="nav-bar">
     <img id="gitLogo" src="data:image/png;base64," alt="" />
    <input class="inputField" type="text" onChange={changeUserName} placeholder="Username"/>
    <input class="inputField" type="text" onChange={changeRepoName} placeholder="Reponame"/>
    <button id="searchButton" onClick={search}>SEARCH</button>
    </div>
    <Issues isFetchDataSuccess={isFetchDataSuccess}  issuesArray={issuesArray} currentPage={currentPage} dataPerPage={dataPerPage}/>
    <Pagination dataPerPage={dataPerPage} totalData={issuesArray.length} changeCurrentPage={changeCurrentPage}/>
    
    </div> );
 
}

export default App;
